function getDataFromServer(code,cb){    //cb: Callback 
	fetch('https://192.168.101.42:3002'+code)
	.then(function(response){
		response.json().then(function(result){
			console.log(result)
			return result
		})
		.then(cb)
	})
}


const Client = {
	getDataFromServer,
}

export default Client

