import React, { Component } from 'react';
import logo from './tigger.svg';
import './App.css';
import Client from './Client'

var parseString = require('xml2js').parseString;

function Currency(props){
  if(props.currencies !== 0){
    return(<select className="form-control" id="key_currency" onChange={props.change}>
         {props.currencies.map(function(row){
           return <option key={row.currency_nr} value={row.currency_nr}>{row.iso_code}</option>
         })}
         </select>
     )
    }
  else{
    return <p></p>
  }
}

function Vesseltype(props){
  if(props.vesseltypes.length !== 0){
    return(<select className="form-control" id="vesseltype" onChange={props.change}>
         {props.vesseltypes.map(function(row){
           return <option key={row.key_vesseltype} value={row.key_vesseltype}>{row.vesseltype}</option>
         })}
         </select>
     )
    }
  else{
    return <p></p>
  }
}

function Country(props){
  if(props.countries !== 0){
    return(<select className="form-control" id="ph_country_code" onChange={props.change}>
         {props.countries.map(function(row){
           return <option key={row.key_staat} value={row.key_staat}>{row.landesbez_int}</option>
         })}
         </select>
     )
    }
  else{
    return <p></p>
  }
}

function CruisingArea(props){
  if(props.areas !== 0){
    return(<select multiple className="form-control" id="cruising_areas">
         {props.areas.map(function(row){
           return <option className='cruising_areas' key={row.cruising_area} value={row.cruising_area} onDoubleClick={props.change}>{row.cruising_area} - {row.short}</option>
         })}
         </select>
     )
    }
  else{
    return <p></p>
  }
}

function SelectionCruisingArea(props){
  if(props.list_cruising_areas.length !== 0){
    return(<select multiple className="form-control" id="list_cruising_areas">
      {props.list_cruising_areas.map(function(row){
           return <option className='list_cruising_areas' key={row} value={row} onDoubleClick={props.change}>{row}</option>
         })}
    </select>
     )
    }
  else{
    return <p></p>
  }
}

function Mandant(props){
  if(props.mandants !== 0){
    return(<select className="form-control" id="mandant" onChange={props.change}>
         {props.mandants.map(function(row){
           return <option key={row.mandant_nr} value={row.ibs_mandant_nr}>{row.name}</option>
         })}
         </select>
     )
    }
  else{
    return <p></p>
  }
}

function Clauses(props){
  if(props.clauses.length !== 0){
    return(<select multiple className="form-control" id="clauses">
         {props.clauses.map(function(row){
           return <option className='clauses' key={row.klausel_nr} value={row.klausel_nr} onDoubleClick={props.change}>{row.klausel_key} {row.Klausel_Kurz_E}</option>
         })}
         </select>
     )
    }
  else{
    return <p>No clauses for this office</p>
  }
}

function SelectionClauses(props){
  if(props.list_clauses.length !== 0){
    return(<select multiple className="form-control" id="list_clauses">
      {props.list_clauses.map(function(row){
           return <option className='list_clauses' key={row} value={row} onDoubleClick={props.change}>{row}</option>
         })}
    </select>
     )
    }
  else{
    return <p></p>
  }
}

function Tarif(props){
  if(props.submit !== 0 && props.variants.length !== 0){
    return(<div>
    	<table className="table table-bordered">
          <tr>
            <td><h3>Premium</h3></td>
            <td><h3>Rate</h3></td>
            <td><h3>Deductible</h3></td>
          </tr>
          {props.variants.map(function(row){
            return(<tr>
              <td>{row.premium}</td>
              <td>{(row.rate*100).toFixed(4)} %</td>
              <td>{row.deductible}</td>
            </tr>)
          })}
         </table>

         {props.methods.map((row)=>{
          return(<p>{row}</p>)
         })}


         
         <div className="row form-group">
			<div className="col-sm-4">
			  {props.variants[0].premium+(props.variants[0].deductible-props.ded)/5}
			</div>
			<div className="col-sm-4">
			  <input type="text" className="form-control" id="calc_ded" placeholder="Enter deductible" onChange={props.change}/>
			</div>
		</div>
	</div>)
  }
  else{
    return <p></p>
  }
}


class App extends Component {

  constructor(){
    super();

    this.state = {
      currencies: 0,
      countries: 0,
      cruisingAreas: 0,
      mandants: 0,
      clauses: [],
      vesseltypes: [],
      submit: 0,
      variants: [],
      calc_ded: 0,
      methods: [],

      mast_yn: 1,
      loa: 20,
      key_currency: 20,
      displacement_t: 20,
      sailarea_sqm: 20,
      total_engine_power_kw: 20,
      fixed_value_cc: 100000,
      ph_country_code: 'A',
      list_cruising_areas: [],
      list_clauses: [],
      mandant: 200.0000
      
    };

  this.getInitialData();

  this.handleMandant = this.handleMandant.bind(this);
  this.handleSubmit = this.handleSubmit.bind(this);
  this.handleChange = this.handleChange.bind(this);
  this.handleDoubleClickAdd = this.handleDoubleClickAdd.bind(this);
  this.handleDoubleClickRem = this.handleDoubleClickRem.bind(this);
  this.readXml = this.readXml.bind(this);
  this.addMethods = this.addMethods.bind(this);

  }

  getInitialData(){
    Client.getDataFromServer('/initialData',(data) => {
      this.setState({
        currencies: data[0],
        countries: data[1],
        cruisingAreas: data[2],
        mandants: data[3],
        clauses: data[4],
        vesseltypes: data[5]
      })
    })

  }

  handleMandant(event){
    this.setState({
      mandant: event.target.value
    });

    Client.getDataFromServer('/clauses?mandant_nr=' + event.target.value, (data) => {
      this.setState({
        clauses: data
      })
    })
  }

  handleDoubleClickAdd(event){
    switch(event.target.className){
      case 'cruising_areas': 
        var arrayvar = this.state.list_cruising_areas;
        arrayvar.push(event.target.value)
        this.setState({ list_cruising_areas: arrayvar })
        break;
      case 'clauses':
        var arrayvar = this.state.list_clauses;
        arrayvar.push(event.target.value)
        this.setState({ list_clauses: arrayvar })
        break;
    }
  }

  handleDoubleClickRem(event){
    switch(event.target.className){
      case 'list_clauses':
        var arrayvar = this.state.list_clauses;
        var index = arrayvar.indexOf(event.target.value);
        if (index > -1) {
              arrayvar.splice(index, 1)
            this.setState({ list_clauses: arrayvar })  
        }
        break;
      case 'list_cruising_areas':
        var arrayvar = this.state.list_cruising_areas;
        var index = arrayvar.indexOf(event.target.value);
        if (index > -1) {
              arrayvar.splice(index, 1)
            this.setState({ list_cruising_areas: arrayvar })  
        }
        break;
    }
    
  }

  handleSubmit(){
    var list_cruising_areas = ''
    this.state.list_cruising_areas.forEach(function(x){
      list_cruising_areas += '{'+x+'},'
    })
    list_cruising_areas = list_cruising_areas.slice(0,-1)

    var list_clauses = ''
    this.state.list_clauses.forEach(function(x){
      list_clauses += '{'+x+'},'
    })
    list_clauses = list_clauses.slice(0,-1)

    Client.getDataFromServer('/calculate?mast_yn='+this.state.mast_yn+
                            '&loa='+this.state.loa+
                            '&key_currency='+this.state.key_currency+
                            '&displacement_t='+this.state.displacement_t+
                            '&sailarea_sqm='+this.state.sailarea_sqm+
                            '&total_engine_power_kw='+this.state.total_engine_power_kw+
                            '&fixed_value_cc='+this.state.fixed_value_cc+
                            '&ph_country_code='+this.state.ph_country_code+
                            '&list_cruising_areas='+list_cruising_areas+
                            '&list_clauses='+list_clauses+
                            '&mandant='+this.state.mandant
      ,(data) => {

      if(data.length != 0){
        this.readXml(data[0].Output,(result)=>{
          this.addMethods(result)
        })
      }

      this.setState({
      submit: 1,
      variants: data
      });
  })
  }

  handleChange(element) {
        this.setState({[element.target.id]: element.target.value});
  }

  readXml(xml,cb){
    parseString(xml, function (err, result) {
      if(err) console.log(err)
      var result = result.pcalc.tarifAdditions[0].add
      cb(result);
    })
  }

  addMethods(arr){
    var meth = [];
    arr.map((row)=>{
      meth.push(row.method_name[0]);
      this.setState({methods: meth})
    })  
  }

  render() {

    return (
<div className="App">
        
<div className="jumbotron text-center">
  <h2>Tarif Calculator Alpha</h2> 
</div>
  
<div className="container">

  <form >
    
    <div className="row form-group">
       <div className="col-sm-3">
        <label htmlFor="mast_yn">Sailing or Motor</label>
        <select className="form-control" id="mast_yn" onChange={this.handleChange} >
          <option value='1'>Sailing Boat</option>
          <option value='0'>Motor Boat</option>
        </select>
      </div>
      <div className="col-sm-1"></div>
      <div className="col-sm-3">
        <label htmlFor="vesseltype">Vesseltype</label>
        <Vesseltype vesseltypes={this.state.vesseltypes} change={this.handleChange} />
      </div>
      <div className="col-sm-1">
      </div>
      <div className="col-sm-3">
        <label htmlFor="key_currency">Currency</label>
        <Currency currencies={this.state.currencies} change={this.handleChange} />
      </div>
    </div>

    <div className="row form-group">
    <div className="col-sm-4">
      <label htmlFor="loa">Length:</label>
      <input type="text" className="form-control" id="loa" placeholder="Enter length" onChange={this.handleChange}/>
    </div>
    <div className="col-sm-4">
      <label htmlFor="displacement_t">Displacement:</label>
      <input type="text" className="form-control" id="displacement_t" placeholder="Enter displacement" onChange={this.handleChange}/>
    </div>

    <div className="col-sm-4">
      <label htmlFor="sailarea_sqm">Sail Area:</label>
      <input type="text" className="form-control" id="sailarea_sqm" placeholder="Enter sail area" onChange={this.handleChange} />
    </div>
    </div>

    <div className="row form-group">
    <div className="col-sm-4">
      <label htmlFor="total_engine_power_kw">Power (KW):</label>
      <input type="text" className="form-control" id="total_engine_power_kw" placeholder="Enter power" onChange={this.handleChange}/>
    </div>
    <div className="col-sm-4">
      <label htmlFor="fixed_value_cc">Fixedvalue:</label>
      <input type="text" className="form-control" id="fixed_value_cc" placeholder="Enter fixedvalue" onChange={this.handleChange}/>
    </div>

    <div className="col-sm-4">
      <label htmlFor="ph_country_code">Country:</label>
      <Country countries={this.state.countries} change={this.handleChange}/>
    </div>

    </div>

    <div className="row form-group">
    <div className="col-sm-5">
      <label htmlFor="cruising_areas">Cruising Areas:</label>
      <CruisingArea areas={this.state.cruisingAreas} change={this.handleDoubleClickAdd} />
    </div>

    <div className="col-sm-3">
      <label htmlFor="list_cruising_areas">Selected Areas:</label>
      <SelectionCruisingArea list_cruising_areas={this.state.list_cruising_areas} change={this.handleDoubleClickRem} />
    </div>
    </div>

    <div className="row form-group">
    <div className="col-sm-2">
      <label htmlFor="mandant">Office:</label>
      <Mandant mandants={this.state.mandants} change={this.handleMandant} />
    </div>
    <div className="col-sm-3">
      <label htmlFor="clauses">Clauses:</label>
      <Clauses clauses={this.state.clauses} change={this.handleDoubleClickAdd}/>
    </div>
    <div className="col-sm-3">
      <label htmlFor="list_clauses">Selected Clauses:</label>
      <SelectionClauses list_clauses={this.state.list_clauses} change={this.handleDoubleClickRem} />
    </div>
    </div>

    <button type="button" className="btn btn-default" onClick={this.handleSubmit}>Submit</button>

  </form>

</div>

<div className="container">
  <h2>Result</h2>
  <Tarif variants={this.state.variants} ded={this.state.calc_ded} methods={this.state.methods} submit={this.state.submit} change={this.handleChange} read={this.readXml} add={this.addMethods}/>
</div>

</div>

    );
  }
}

export default App;





